import { Grid, TextField } from "@material-ui/core";
import React, { useState } from "react";
import styled from "styled-components";
import AttachmentTab from "./Workflow components/Attachments";
import RemarksTab from "./Workflow components/Remarks";

const Workflow = ({ data }) => {
  const [selected, setSelected] = useState("remarks");

  console.log("Data => ", data);

  const [projectData] = data;

  return (
    <div>
      <div style={{ marginTop: "20px", padding: "10px" }}>
        <Grid spacing={3} container>
          <Grid item xs={4}>
            <TextField
              fullWidth
              label="Visit Number"
              variant="outlined"
              disabled={true}
              value={projectData?.VisitNumber}
            />
          </Grid>
          <Grid item xs={4}>
            <TextField
              fullWidth
              label="Client Name"
              variant="outlined"
              disabled={true}
              value={projectData?.ClientName}
            />
          </Grid>
          <Grid item xs={4}></Grid>
        </Grid>
      </div>
      <WrapperComponent>
        <div>
          <TabButton
            onClick={() => {
              setSelected("remarks");
            }}
            selected={selected === "remarks"}
          >
            Remarks
          </TabButton>
          <TabButton
            onClick={() => {
              setSelected("attachment");
            }}
            selected={selected === "attachment"}
          >
            Attachments
          </TabButton>
        </div>

        {selected === "remarks" && <RemarksTab />}
        {selected === "attachment" && <AttachmentTab />}
      </WrapperComponent>
    </div>
  );
};

export default Workflow;

const WrapperComponent = styled.div`
  margin-top: 10px;
  padding: 10px;
`;

const TabButton = styled.button`
  background-color: ${(props) => (props.selected ? "#1D56B3" : "#262b36")};
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 500;
  margin: 2px;
  outline: none;
  border: none;
  cursor: pointer;
`;
