import React, {useState, useEffect} from "react";
import {withRouter} from "react-router-dom";
import {
    Grow,
    Typography,
    Grid,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    CircularProgress,
    Button,
    Input,
} from "@material-ui/core";

import {GetAllClients} from "../../server";
import {styles} from "../../styles";

function SearchClientComponent(props) {
    const {id} = props.match.params;
    const [data, setData] = useState([]);
    const [projects, setProjects] = useState([]);
    const [loadingProjects, setLoadingProjects] = useState(false);

    const [Name, setName] = useState("");
    const [phone, setPhone] = useState("");
    const [email, setEmail] = useState("");

    function searchData(e) {
        e.preventDefault();
        GetAllClients(props.page, 10, Name, phone, email)
            .then((res) => {
                if (res.data.Result.result === true) {
                    let users = res.data.Result.ClientsData;
                    let count = res.data.Result.TotalCount;
                    props.setCount(count);
                    props.setLoading(false);
                    props.setData(users);
                } else {
                    let ErrorMessage = res.data.Result.message;
                    props.setLoading(false);
                    props.setError(ErrorMessage);
                }
            })
            .catch((err) => {
                let ErrorMessage = err.message;
                props.setLoading(false);
                props.setError(ErrorMessage);
            });
    }

    function resetData(ev){
        ev.preventDefault();
        setName("");
        setPhone("");
        setEmail("");
        GetAllClients(0, 10)
            .then((res) => {
                if (res.data.Result.result === true) {
                    let users = res.data.Result.ClientsData;
                    let count = res.data.Result.TotalCount;
                    props.setCount(count);
                    props.setLoading(false);
                    props.setData(users);
                } else {
                    let ErrorMessage = res.data.Result.message;
                    props.setLoading(false);
                    props.setError(ErrorMessage);
                }
            })
            .catch((err) => {
                let ErrorMessage = err.message;
                props.setLoading(false);
                props.setError(ErrorMessage);
            });
    }

    return (
        <Grow in={true}>
            <div>
                <Grid container>
                    <Grid item xs={12}>
                        {loadingProjects ? (
                            <div style={{...styles.d_flex, ...styles.justify_center}}>
                                <CircularProgress/>
                            </div>
                        ) : (
                            <Grid
                                container
                                justify="space-between"
                                alignItems="center"
                                style={styles.mb_3}
                            >
                                <Grid item xs={3}>
                                    <FormControl style={{...styles.my_3}} fullWidth>
                                        <InputLabel htmlFor="age-simple">Name</InputLabel>
                                        <Input
                                            value={Name}
                                            onChange={(e) => {
                                                setName(e.target.value);
                                            }}
                                        />
                                    </FormControl>
                                </Grid>
                                <Grid item xs={3}>
                                    <FormControl style={{...styles.my_3}} fullWidth>
                                        <InputLabel htmlFor="age-simple">Phone Number</InputLabel>
                                        <Input
                                            value={phone}
                                            onChange={(e) => {
                                                setPhone(e.target.value);
                                            }}
                                        />
                                    </FormControl>
                                </Grid>
                                <Grid item xs={3}>
                                    <FormControl style={{...styles.my_3}} fullWidth>
                                        <InputLabel htmlFor="age-simple">Email</InputLabel>
                                        <Input
                                            value={email}
                                            onChange={(e) => {
                                                setEmail(e.target.value);
                                            }}
                                        />
                                    </FormControl>
                                </Grid>
                            </Grid>
                        )}
                        {!loadingProjects && (
                            <Grid
                                xs={4}
                                spacing={3}
                                container
                                justify='flex-end'
                                alignItems="center"
                                style={styles.mb_3}
                            >
                                <Grid item xs={6} spacing={3}>
                                    <Button
                                        fullWidth
                                        onClick={resetData}
                                        color="primary"
                                        variant="contained"
                                    >
                                        Reset
                                    </Button>
                                </Grid>
                                <Grid item xs={6} spacing={3}>
                                    <Button
                                        fullWidth
                                        onClick={(e) => {
                                            searchData(e);
                                        }}
                                        color="primary"
                                        variant="contained"
                                        disabled={
                                            Name === "" && email === "" && phone === ""
                                        }
                                    >
                                        Search
                                    </Button>
                                </Grid>
                            </Grid>
                        )}
                    </Grid>
                </Grid>
            </div>
        </Grow>
    );
}

export default withRouter(SearchClientComponent);
