import React, { useEffect, useRef, useState } from "react";
import Axios from "axios";
import Fab from "@material-ui/core/Fab";
import ReactTable from "react-table";
import EditIcon from "@material-ui/icons/Edit";
import CircularProgress from "@material-ui/core/CircularProgress";
import { TableHeader } from "../tableHeader";
import { useDispatch } from "react-redux";
import { useParams, useHistory } from "react-router-dom";
import AddIcon from "@material-ui/icons/Add";
import { URL } from "../../BaseUrl";
import moment from "moment";
import VisitHistoryDialog from "./VisitHistoryDialog";

function VisitHistory() {
  const params = useParams();
  const [open, setOpen] = useState(false);
  const [type, setType] = useState("Add");
  const [dataForEdit, setDataForEdit] = useState({});
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const reactTableRef = useRef(null);

  // Get All Table Data

  useEffect(() => {
    getTableData();
  }, []);

  const getTableData = () => {
    setLoading(true);

    const body = {
      ProjectID: params.id,
    };

    Axios.post(URL + "/api/GetFolowUpClientVisits", body)
      .then((res) => {
        if (res.data.result) {
          setData(res.data.ClientVisitArray);
        } else {
          setData([]);
        }
      })
      .catch((err) => {
        console.error("Error => ", err);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);

    setTimeout(() => {
      setType("Add");
      setDataForEdit({});
    }, 100);
  };

  const columns = [
    {
      Header: <TableHeader name="ID" />,
      accessor: "ID",
      className: "justify-start",
      sortable: false,
    },
    {
      Header: <TableHeader name="Name" />,
      accessor: "ClientName",
      className: "justify-start",
      sortable: false,
    },

    {
      Header: <TableHeader name="Location" />,
      accessor: "",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return (
          <>
            {row.original.Area} {row.original.PlotNo}
          </>
        );
      },
    },

    {
      Header: <TableHeader name="Assign By" />,
      accessor: "CreatedBy",
      className: "justify-start",
      sortable: false,
    },

    {
      Header: <TableHeader name="Visit Type" />,
      accessor: "VisitTypeTitle",
      className: "justify-start",
      sortable: false,
    },

    {
      Header: <TableHeader name="Status" />,
      accessor: "Status",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return <>{row.value ? "True" : "False"}</>;
      },
    },

    {
      Header: <TableHeader name="Created Date" />,
      accessor: "CreatedDate",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return <>{moment(row.value).format("l")}</>;
      },
    },

    {
      Header: <TableHeader name="Edit" />,
      accessor: "edit",
      className: "justify-center",
      sortable: false,
      width: 200,
      Cell: (row) => {
        return (
          <Fab
            variant="extended"
            size="medium"
            color="primary"
            aria-label="edit"
            onClick={() => {
              setDataForEdit(row.original);
              setType("Save");
              handleClickOpen();
            }}
          >
            <EditIcon className="mr-8" />
            Edit
          </Fab>
        );
      },
    },
  ];

  return (
    <div>
      {/* Header */}
      <div
        style={{
          display: "flex",
          justifyContent: "flex-end",
          alignItems: "center",
          marginTop: "15px",
        }}
      >
        <div>
          <Fab
            variant="extended"
            size="medium"
            color="primary"
            aria-label="add"
            onClick={handleClickOpen}
          >
            <AddIcon className="mr-8" />
            Add New
          </Fab>
        </div>
      </div>
      {/* Table */}
      <div
        style={{ marginTop: 20 }}
        className="w-full mt-10 flex justify-between items-center"
      >
        <ReactTable
          className="-striped w-full -highlight h-full overflow-hidden"
          style={{ marginBottom: "20px" }}
          data={data}
          columns={columns}
          defaultPageSize={10}
          loading={loading}
          noDataText="No Data"
          loadingText={<CircularProgress />}
          ref={reactTableRef}
        />
      </div>
      <VisitHistoryDialog
        open={open}
        type={type}
        handleClose={handleClose}
        dataForEdit={dataForEdit}
        getTableData={getTableData}
      />
    </div>
  );
}

export default VisitHistory;
