import React, { useEffect, useRef, useState } from "react";
import Axios from "axios";
import ReactTable from "react-table";
import CircularProgress from "@material-ui/core/CircularProgress";
import { TableHeader } from "../../components/tableHeader";
import {
  Chip,
  FormControl,
  MenuItem,
  Select,
  IconButton,
  Typography,
} from "@material-ui/core";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import { URL } from "../../BaseUrl";
import moment from "moment";
import { useHistory } from "react-router-dom";
import ProjectSearch from "./Project|Search";
import { makeStyles } from "@material-ui/styles";

// eslint-disable-next-line no-undef
const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  chips: {
    height: "100%",
  },
  chip: {
    margin: 2,
    width: "100%",
  },
  noLabel: {
    marginTop: theme.spacing(3),
  },
}));

const ProjectList = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const params = useParams();
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [pages, setPages] = useState(0);
  const reactTableRef = useRef(null);
  const [pageSize, setPageSize] = useState(10);
  const [pageIndex, setPageIndex] = useState(0);
  const [orderby, setOrderBy] = useState("A.ID DESC");
  const [searchObject, setSearchObject] = useState({});
  const [statusSuggestions, setStatusSuggestions] = useState([]);

  useEffect(() => {
    Axios.post(URL + "/Api/GetProjectStage")
      .then((response) => {
        if (response.data) {
          setStatusSuggestions(response.data);
        }
      })
      .catch((err) => console.error(err));
  }, []);

  // Get All Table Data

  useEffect(() => {
    getTableData();
  }, [pageIndex, pageSize, searchObject, orderby]);

  const getTableData = () => {
    setLoading(true);
    const body = {
      nKeywordTitle: searchObject.nKeywordTitle || "",
      nProjectType: "",
      nProjectID: null,
      nClientID: null,
      nDateFrom: "",
      nDateTo: "",
      nContractType: "",
      nMobileNumber: searchObject.nMobileNumber || "",
      nPlotNo: searchObject.nPlotNo || "",
      nStatus: "",
      nOrderBy: orderby,
      nPageIndex: pageIndex,
      nPageSize: pageSize,
      nStage: searchObject.nStage,
    };

    Axios.post(URL + "/api/GetAllProjects", body)
      .then((res) => {
        if (res.data.Result.result) {
          setData(res.data.Result.ProjectsData);
          setPages(res.data.Result.TotalPages);
        } else {
          setData([]);
          setPages(0);
        }
      })
      .catch((err) => {
        console.error("Error => ", err);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  function getStatusName(Id) {
    if (statusSuggestions && statusSuggestions.length > 0) {
      const status = statusSuggestions.find((stat) => stat.ID === Id);
      return status?.Stage;
    }
  }

  function updateStatus(ev, data) {
    const body = {
      nProjectID: data.ID,
      nStageID: ev.target.value,
    };
    Axios.post(URL + "/api/UpdateProjectStage", body)
      .then((response) => {
        if (response.data.Result.result) {
          getTableData();
        } else {
          console.log(response.data.Result.message);
        }
      })
      .catch((err) => console.error(err));
  }

  const columns = [
    {
      Header: <TableHeader name="ID" />,
      accessor: "ID",
      className: "justify-start",
      sortable: true,
      defaultSortDesc: true,
    },
    {
      Header: <TableHeader name="Name" />,
      accessor: "ProjectTitle",
      className: "justify-start",
      sortable: false,
    },

    {
      Header: <TableHeader name="Location" />,
      accessor: "",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return (
          <>
            {row.original.Area} {row.original.PlotNo}
          </>
        );
      },
    },

    {
      Header: <TableHeader name="Assign By" />,
      accessor: "CreatedBy",
      className: "justify-start",
      sortable: false,
    },

    {
      Header: <TableHeader name="Start Date" />,
      accessor: "StartDate",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return <>{moment(row.value).format("l")}</>;
      },
    },
    {
      Header: <TableHeader name="End Date" />,
      accessor: "EndDate",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return <>{moment(row.value).format("l")}</>;
      },
    },
    {
      Header: <TableHeader name="Status" />,
      accessor: "Stage",
      className: "justify-start",
      sortable: false,
      width: 400,
      Cell: (row) => (
        <div
          className="flex items-center"
          onClick={(ev) => {
            ev.stopPropagation();
          }}
        >
          <FormControl fullWidth className={classes.formControl}>
            <Select
              fullWidth
              id="demo-mutiple-chip"
              value={row.value}
              onChange={(e) => updateStatus(e, row.original)}
              renderValue={(selected) => (
                <div className={classes.chips}>
                  <Chip
                    size="large"
                    label={getStatusName(selected)}
                    color="secondary"
                    className={classes.chip}
                  />
                </div>
              )}
            >
              {statusSuggestions ? (
                statusSuggestions.map((status) => (
                  <MenuItem key={status.ID} value={status.ID}>
                    {status.Stage}
                  </MenuItem>
                ))
              ) : (
                <div>No Status available!</div>
              )}
            </Select>
          </FormControl>
        </div>
      ),
    },

    {
      Header: <TableHeader name="Created Date" />,
      accessor: "CreatedDate",
      className: "justify-start",
      sortable: false,
      Cell: (row) => {
        return <>{moment(row.value).format("l")}</>;
      },
    },
  ];
  const handleSearch = (model) => {
    const searchObj = { ...model };
    setSearchObject(searchObj);
  };

  const resetSearch = () => {
    setSearchObject({});
    setPageIndex(0);
    setPageSize(reactTableRef.current.state.pageSize);
  };

  return (
    <div>
      {/* Header */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <div>
          <h1>Projects</h1>
        </div>
      </div>
      <ProjectSearch handleSearch={handleSearch} resetSearch={resetSearch} />
      {/* Table */}
      <div
        style={{ marginTop: 20 }}
        className="w-full mt-10 flex justify-between items-center"
      >
        <ReactTable
          className="-striped w-full -highlight h-full overflow-hidden"
          data={data}
          columns={columns}
          defaultPageSize={10}
          loading={loading}
          noDataText="No Data"
          loadingText={<CircularProgress />}
          manual
          multiSort={false}
          defaultSorted={[
            {
              // the sorting model for the table
              id: "ID",
              desc: true,
            },
          ]}
          pages={pages}
          ref={reactTableRef}
          onPageChange={(e) => {
            setPageIndex(e);
          }}
          onPageSizeChange={(pagesize, pagenumber) => {
            setPageSize(pagesize);
            setPageIndex(pagenumber);
          }}
          onSortedChange={(value) => {
            if (value.find((val) => val.id === "ID")) {
              value.find((val) => val.id === "ID").desc
                ? setOrderBy("A.ID DESC")
                : setOrderBy("A.ID");
            }
          }}
        />
      </div>
    </div>
  );
};

export default ProjectList;
