import React, {useEffect, useState} from "react";
import {makeStyles} from "@material-ui/core/styles";
import {withRouter} from "react-router-dom";
import {
    Grow,
    Grid,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Button,
    Input,
} from "@material-ui/core";
import {GetAllUsersPagged,} from "../../server";
import {styles} from "../../styles";

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: "relative",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: "33.33%",
        flexShrink: 0,
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },
    expansionPanel: {
        marginBottom: 16,
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
}));

function UsersSearch({setData, page, setError, setLoading, setCount, roles}) {
    const classes = useStyles();

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phoneNo, setPhoneNo] = useState('');
    const [role, setRole] = useState('');

    useEffect(()=>{
        console.log(roles);
    },[roles])

    function searchData() {
        setLoading(true)
        GetAllUsersPagged(page, name, email, phoneNo, role)
            .then((res) => {
                if (res.data.Result.result === true) {
                    let users = res.data.Result.UsersData;
                    let count = res.data.Result.TotalCount;
                    setCount(count);
                    setLoading(false);
                    setData(users);
                } else {
                    let ErrorMessage = res.data.Result.message;
                    setLoading(false);
                    setError(ErrorMessage);
                }
            })
            .catch((err) => {
                let ErrorMessage = err.message;
                setLoading(false);
                setError(ErrorMessage);
            });
    }

    function resetData() {
        setLoading(true)
        setName("");
        setEmail("");
        setRole("");
        setPhoneNo("");
        GetAllUsersPagged(0)
            .then((res) => {
                if (res.data.Result.result === true) {
                    let users = res.data.Result.UsersData;
                    let count = res.data.Result.TotalCount;
                    setCount(count);
                    setLoading(false);
                    setData(users);
                } else {
                    let ErrorMessage = res.data.Result.message;
                    setLoading(false);
                    setError(ErrorMessage);
                }
            })
            .catch((err) => {
                let ErrorMessage = err.message;
                setLoading(false);
                setError(ErrorMessage);
            });
    }

    return (
        <Grow in={true}>
            <div>
                <Grid container>
                    <Grid item xs={12}>
                        <Grid
                            container
                            justify="space-between"
                            alignItems="center"
                            spacing={2}
                            style={styles.mb_3}
                        >
                            <Grid item xs={3}>
                                <FormControl style={{...styles.my_3}} fullWidth>
                                    <InputLabel htmlFor="age-simple">Name</InputLabel>
                                    <Input
                                        value={name}
                                        onChange={(ev) => setName(ev.target.value)}
                                    />
                                </FormControl>
                            </Grid>
                            <Grid item xs={3}>
                                <FormControl
                                    className={classes.formControl}
                                    style={{...styles.my_3}}
                                    fullWidth
                                >
                                    <InputLabel htmlFor="roleId">Role</InputLabel>
                                    <Select
                                        value={role}
                                        onChange={(e) => {
                                            setRole(e.target.value);
                                        }}
                                    >
                                        {roles && roles.map((item) => {
                                            return (
                                                <MenuItem value={item.Id}>{item.Name}</MenuItem>
                                            );
                                        })}
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={3}>
                                <FormControl style={{...styles.my_3}} fullWidth>
                                    <InputLabel htmlFor="age-simple">Email</InputLabel>
                                    <Input
                                        value={email}
                                        onChange={(ev) => setEmail(ev.target.value)}
                                    />
                                </FormControl>
                            </Grid>
                            <Grid item xs={3}>
                                <FormControl style={{...styles.my_3}} fullWidth>
                                    <InputLabel htmlFor="age-simple">Phone No.</InputLabel>
                                    <Input
                                        value={phoneNo}
                                        type='number'
                                        onChange={(ev) => setPhoneNo(ev.target.value)}
                                    />
                                </FormControl>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={4} container justify='space-between' alignItems='center' style={styles.mb_3}>
                        <Grid xs={5}>
                            <Button
                                fullWidth
                                onClick={resetData}
                                color="primary"
                                variant="contained"
                            >
                                Reset
                            </Button>
                        </Grid>
                        <Grid xs={5}>
                            <Button
                                fullWidth
                                onClick={searchData}
                                color="primary"
                                variant="contained"
                                disabled={
                                    name === "" &&
                                    email === "" &&
                                    phoneNo === "" &&
                                    role === ""
                                }
                            >
                                Search
                            </Button>
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        </Grow>
    );
}

export default withRouter(UsersSearch);
